import Image from "next/image";
import logo_text from "../../public/Logos/Logo_Text_svg.svg"
export default function Header() {
    return (
        <div className="w-full h-[5vw] content-center  backdrop-blur-md backdrop-brightness-150 sticky top-0">
            <div className="container">
                <Image alt="logo" src={logo_text} width={700} height={700} className=""/>
            </div>
        </div>
    );
}